.logo {
  margin-right: 40px;
  &--desktop {
    display: block;
    width: 70px;
    height: 38px;
  }

  &--tablet,
  &--mobile {
    display: none;
  }

  @media (max-width: $vp-1023) {
    margin-right: 25px;
    &--tablet {
      display: block;
      width: 38px;
      height: 20px;
    }

    &--desktop,
    &--mobile {
      display: none;
    }
  }

  @media (max-width: $vp-767) {
    &--mobile {
      display: block;
      width: 42px;
      height: 22px;
    }

    &--desktop,
    &--tablet {
      display: none;
    }
  }
}
