.adress__wrapper {
  max-width: $vp-1366;
  min-width: $vp-320;

  margin: 0 auto;

  display: grid;
  grid-template-columns: 560px 1fr;
  column-gap: 100px;
  align-items: center;
  justify-content: center;

  padding: 120px 103px;
  padding-bottom: 109px;

  color: $color-tangaroa;

  @media (max-width: $vp-1365) {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;

    padding-top: 80px;
    padding-bottom: 89px;
  }

  @media (max-width: $vp-1023) {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;

    padding: 0 45px;
    padding-top: 80px;
    padding-bottom: 89px;
  }

  @media (max-width: $vp-767) {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 316px;

    padding: 65px 15px;
    padding-top: 57px;
    padding-bottom: 90px;
  }
}

.adress iframe {
  justify-self: stretch;

  height: 305px;

  @media (max-width: $vp-1023) {
    justify-self: center;

    width: 100%;
    height: 335px;
  }

  @media (max-width: $vp-767) {
    height: 316px;

    margin-top: 43px;

    order: 2;
  }
}

.adress__text {
  display: flex;
  flex-direction: column;
  row-gap: 27px;

  padding-top: 75px;
  padding-bottom: 85px;

  font-size: 16px;
  font-weight: 400;
  line-height: 24px;

  @media (max-width: $vp-1023) {
    row-gap: 23px;

    padding-top: 84px;
    padding-bottom: 80px;
  }

  @media (max-width: $vp-767) {
    justify-self: center;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 24px;

    padding-top: 0;
    padding-bottom: 10px;
  }
}

.adress__text header {
  font-size: 18px;
  font-weight: 500;
  line-height: 27.5px;
}

.adress__geolocation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 10px;

  @media (max-width: $vp-1023) {
    align-items: flex-start;

    margin-top: 3px;
  }

  @media (max-width: $vp-767) {
    align-items: flex-start;

    margin-top: 0;
  }
}

.adress__geolocation svg {
  @media (max-width: $vp-1023) {
    margin-top: 8px;
  }
}

.adress__phone {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 10px;

  @media (max-width: $vp-767) {
    align-items: flex-start;
  }
}
